<template>
  <div id="main_listadv">
    <div id="filterlogo_listadv">
      <p id="filtertext_listadv">Select an adventurer</p>
      <button id="filterbutton_listadv" @click="goFilter()">Open Filter</button>
    </div>
    <div id="filter_listadv">
      <input v-model="filterInput" type="text" placeholder="Put an adventurer name or title..." @change="searchFilter">
      <button id="loadfilterButton_listadv" @click="searchFilter()">Load</button>
    </div>
    <n-scrollbar id="main_outlist_listadv" trigger="none">
        <div id="main_inlist_listadv">
          <div v-if="dataAdv.length == 0">
            <p>
              Loading...
            </p>
          </div>
          <n-grid :x-gap="0" :y-gap="0" :cols="n_columns" >
            <n-grid-item v-for="char in filteredData" :key="char.id_order" id="cell_listadv">
              <div id="imagecell_listadv" @click='gotoCharPage(char.id_adventurers)' :title="'[' + char.title + '] ' + char.name">
                <img alt="Frame" :src="decideBase(char.stars, char.have_hero_asc, char.hero_festa)" >
                <!-- <img :alt="char.id_intern" :src="getImg(char.id_intern)" class="char_img"> -->
                <img alt="Frame_front" :src="decideBaseFrame(char.stars, char.have_hero_asc, char.hero_festa)">
              </div>
            </n-grid-item>
          </n-grid>
        </div>
    </n-scrollbar>
  </div>
</template>

<script>
import { NScrollbar, NGrid, NGridItem, NButton, NTooltip } from 'naive-ui'
import axios from 'axios'

export default {
  components: { NScrollbar, NGrid, NGridItem, NButton, NTooltip },
  data() {
    return {
      dataAdv: [],
      filteredData: [],
      filterInput: "",
      n_columns: 9,

      screenWidth: window.innerWidth
    }
  },
  beforeMount() {
    let adv_list = window.sessionStorage.getItem("adv_list")
    if(adv_list == null){
      var data = require('../data/adv_list_DATA.json')
      this.dataAdv = data
      this.filteredData = data
      window.sessionStorage.setItem("adv_list", JSON.stringify(data));
      // axios.get('http://172.16.30.62:8082/list_aventureiros').then(response => {
      //   this.dataAdv = response.data
      //   this.filteredData = response.data
      //   window.sessionStorage.setItem("adv_list", JSON.stringify(response.data));
      // })
    }else{
      this.dataAdv = JSON.parse(adv_list)
      this.filteredData = JSON.parse(adv_list)
    }
    this.onResize()
  },
  mounted() {
    this.$nextTick(() => {
      window.addEventListener('resize', this.onResize);
    })
  },
  beforeDestroy() { 
    window.removeEventListener('resize', this.onResize); 
  },
  methods: {
    onResize() {
      this.screenWidth = window.innerWidth

      if(this.screenWidth > 1000){
        this.n_columns = 9
      }else if(this.screenWidth > 785){
        this.n_columns = 7
      }else if(this.screenWidth > 600){
        this.n_columns = 5
      }else{
        this.n_columns = 3
      }
    },
    getImg(url){
      return require('../assets/adventurers/hex_adv/' + url + '.png')
    },
    gotoCharPage(char_id){
      this.$router.push({ name: "adventurer", params: {id: char_id} });
    },
    searchFilter(){
      let filtered = this.dataAdv
      let filterLower = this.filterInput.toLowerCase()
      let characters = filtered.filter((char) => {
        return char.name.toLowerCase().indexOf(filterLower) > -1 || char.title.toLowerCase().indexOf(filterLower) > -1
      })
      this.filteredData = characters
    }, 

    goFilter(){
      this.$router.push({ name: "filter_adv"});
    },

    decideBase(star, hero_asc, hero_festa){
      if(!false){
        switch (star) {
          case 1:
            return require('../assets/frame/frame_1.png')
          case 2:
            return require('../assets/frame/frame_2.png')
          case 3:
            return require('../assets/frame/frame_3.png')  
          case 4:
            if(hero_festa){
              return require('../assets/frame/frame_4_festa.png')
            }
            if(!hero_asc){return require('../assets/frame/frame_4.png')}
            else{return require('../assets/frame/frame_4_star.png')}
          default:
            return require('../assets/frame/frame_1.png')    
        }
      }
    },
    decideBaseFrame(star, hero_asc, hero_festa){
      if(!false){
        switch (star) {
          case 1:
            return require('../assets/frame/base_frame_1.png')
          case 2:
            return require('../assets/frame/base_frame_2.png')
          case 3:
            return require('../assets/frame/base_frame_3.png')  
          case 4:
            if(hero_festa){
              return require('../assets/frame/frame_4_festa.png')
            }
            if(!hero_asc){return require('../assets/frame/base_frame_4.png')}
            else{return require('../assets/frame/base_frame_4_star.png')}
          default:
            return require('../assets/frame/base_frame_1.png')    
        }
      }
    }

  },

}
</script>

<style>

/* Main Style */

#main_listadv{
  position: absolute;
  min-height: 100%;
  min-width: 100%;
  height: 100%;
  width: 100%;
  background-image: linear-gradient(135deg, #eae9e5 25%, #e0dfdb 25%, #e0dfdb 50%, #eae9e5 50%, #eae9e5 75%, #e0dfdb 75%, #e0dfdb 100%);
  background-size: 14px 14px;
  overflow-y: auto;
  overflow-x: hidden;
}

#filterlogo_listadv{
  position: relative;
  margin: 120px auto 0;
  max-width: 1004px;
  min-width: 316px;
  width: calc(95% + 2px);
  height: 72px;
  border: black 1px solid;
  border-radius: 3px;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  background-color: #e6e6e6;
  color: black;
  display: flex;
  align-items: center;
  vertical-align: middle;
}

/* Filter Logo Section */

#filtertext_listadv {
  margin-left: 2%;
  font-weight: bold;
  font-size: 20px;
  font-family: "wildwords";
  vertical-align: middle;
}

#filterbutton_listadv {
  position: absolute;
  height: 40px;
  width: 100px;
  right: 0px;
  margin-right: 2%;
  transition: 0.2s;
  border: #767676 1px solid;
  border-radius: 2px;
  background-color: #efefef;
  cursor: pointer;
}
#filterbutton_listadv:hover {
  box-shadow: 0px 0px 1px inset black;
  background-color: #e5e5e5;
}

/* Filter Section */

#loadfilterButton_listadv{
  margin: 6px;
  height: 40px;
  width: 100px;
  transition: 0.2s;
  border: #767676 1px solid;
  border-radius: 2px;
  background-color: #efefef;
  cursor: pointer;
}
#loadfilterButton_listadv:hover{
  box-shadow: 0px 0px 1px inset black;
  background-color: #e5e5e5;
}

#filter_listadv{
  margin: 0px auto 0px;
  max-width: 1004px;
  min-width: 316px;
  width: calc(95% + 2px);
  background-color: #e6e6e6;
  border: black 1px solid;
  border-top: 0px;
  position: relative;
  display: flex;
  align-items: center;
  vertical-align: middle;
}

#filter_listadv input{
  margin: 6px 0px 6px 2%;
  height: 40px;
  width: 30%;
  min-width: 220px;
  max-width: 380px;
}

/* Scrollbar Section */

#main_outlist_listadv{
  margin: 0px auto 0px;
  height: calc(100% - 253px);
  max-width: 1000px;
  min-width: 316px;
  width: 95%;
  /* background: linear-gradient(180deg, rgba(247,247,247,1) 0%, rgba(247,247,247,1) 90%, rgba(255,255,255,0) 100%); */
  background-color: rgba(247,247,247,1);
  border: 1px solid black;
  /* border-image: linear-gradient(180deg, #98895c 0%, rgba(255,255,255,0) 100%) 1; */
  border-bottom: none;
  border-top: none;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  overflow: auto;
}

#main_inlist_listadv{
  margin: 0px 10px;
  padding-top: 20px;
  padding-bottom: 400px;
  overflow: auto;
}

#cell_listadv {
  position: relative;
  width: 100px;
  height: 100px;
  margin-bottom: 20px;
}

#imagecell_listadv{
  list-style: none;
  cursor: pointer;
}

#imagecell_listadv img{
  height: 100px;
  width: 100px;
  position: absolute;
  top: 0;
  left: 0;
}

</style>